.block {
  position: absolute;
  top: 0;
  left: 0;
  width: 190px;
  height: 100%;
  z-index: 997;
  background-color: #16192a;
}

.block .menu {
  margin-top: 70px;
  padding-left: 20px;
}

.block .menu .title {
  font-size: 13px;
  font-weight: 500;
  color: #656678;
}

.block .menu .nav ol {
  padding: 0;
  list-style: none;
  color: #cfd2e3;
}